.title {
  display: inline-block;
  color: var(--blue-color);
  padding: 0 0 12px 0;
  margin-bottom: 20px;
  border-bottom: 3px solid var(--yellow-color-50);
  text-transform: uppercase;
  font-size: 30px;
  font-weight: 300;
  line-height: 100%;
  align-self: center;

  @media screen and (min-width: 768px) {
    font-size: 50px;
    padding: 0 0 20px 0;
    margin-bottom: 50px;
    border-bottom: 5px solid var(--yellow-color-50);
  }

  @media screen and (min-width: 1440px) {
    font-size: 60px;
    margin-bottom: 80px;
  }
}

.subtitle {
  display: inline-block;
  padding: 0 0 16px 0;
  border-bottom: 3px solid var(--yellow-color-50);
  font-size: 24px;
  font-weight: 400;

  @media screen and (min-width: 768px) {
    font-size: 36px;
    padding: 0 0 24px 0;
    border-bottom: 5px solid var(--yellow-color-50);
  }

  @media screen and (min-width: 1440px) {
    font-size: 50px;
    padding: 0 0 40px 0;
  }
}
